import { Employee, TaskStatus } from "generated/prisma";
import { CreateTaskDto } from "../types/dtos/task.dto";
import { ExpandedEmployee } from "../types/prisma.types";

export type TaskForm = {
  ticketId: number;
  title: string;
  description: string;
  status: TaskStatus;
  dueDate: string | null;
  assignee: Employee | ExpandedEmployee | null;
};

export const getDefaultTaskForm = (): TaskForm => ({
  ticketId: 0,
  title: '',
  description: '',
  status: TaskStatus.TODO,
  dueDate: null,
  assignee: null,
});

export const convertTaskFormToDto = (form: TaskForm): CreateTaskDto => ({
  ticketId: form.ticketId,
  title: form.title,
  description: form.description || null,
  status: form.status,
  dueDate: form.dueDate,
  assigneeId: form.assignee ? form.assignee.id : null,
});